*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background:#3498db;
}
#header{
    font-size: 64px;
}
#board{
    display: flex;
    width: 450px;
    flex-wrap: wrap;
}
.square{
    width: 150px;
    height: 150px;
    border: 3px dashed  #fff ;
    display: flex;
    justify-content: center;
    font-size: 45px;
    align-items: center;
    cursor: pointer;
    user-select: none;
}
.square:hover{
    background-color: #2980b9;
}
#game{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #fff
}
.square:nth-child(7),.square:nth-child(8),.square:nth-child(9){
    border-bottom: 0;
}
.square:nth-child(1),.square:nth-child(2),.square:nth-child(3){
    border-top: 0;
}
.square:nth-child(3n+1){
    border-left: 0;
}
.square:nth-child(3n){
    border-right: 0;
}